import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';

@Injectable({
    providedIn: 'root'
  })
export class EmailLayoutMasterService {

    constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) {
    }
    public bmlfDeleteEmailLayoutMaster(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFDeleteEmailLayout`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfSaveEmailLayoutMaster(headerData: any, formMode) {
        if (formMode == "I") {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFCreateEmailLayout`;
            return this.httpExt.post<any>(url, headerData, null);
        } else {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFUpdateEmailLayout`;
            return this.httpExt.post<any>(url, headerData, null);
        }
    }

    public bmlfBMGFGetEmailLayoutMaster(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetEmailLayout`;
        return this.httpExt.post<any>(url, headerData, null);
    }
}
