import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';
import { Observable } from 'rxjs';
import { ODataModel } from 'src/app/core/models/odata.model';

@Injectable({
  providedIn: 'root'
})
export class DocumentTagSetupService {

  constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) { }

  // public bmlfGetSetupdData(reqData) : Observable<any> {
  //   const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetDocTagSetup`;
  //   return this.httpExt.get(url, null);
  // }
  public bmlfGetSetupdData(headerData: any) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetDocTagSetup`;
    return this.httpExt.post<any>(url, headerData, null);
}

  public bmlfSaveSetupData(reqData) : Observable<any>{
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/CreateDocTagSetup`;
    return this.httpExt.post<any>(url , reqData, null)
  }

}
