  bmlfSubmitData(): void {

    if (!this.objUtils.mbWrite) {
      this.objToastService.notifyWarning(this.objUtils.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };
    if (this.mbChange == false && (this.msFormMode == 'U')) {
      return;
    }
    if (!this.bmlfValidData()) {
      this.mbIsSearchMode = false;
      this.mbIsAddMode = false;
      this.mbIsCopyMode = false;
      this.mbIsDeleteMode = false;
      this.mbIsCloseMode = false;
      return;
    }
    this.bmlfSaveDashboardEntryData();
  }
  bmlfSaveDashboardEntryData(): void {
    this.mbIsLoading = true;
    let parrFormData = [];
    parrFormData.push(Object.assign({}, this.mObjModel));
    parrFormData[0].Active = this.mObjModel.Active ? "1" : "0";
    let pobjJData = {
      HeaderData: parrFormData,
      Response: [{
        "ResponseText": "",
        "ErrorCode": ""
      }]
    };
    let pobjJson = {
      JSON: JSON.stringify(pobjJData)
    };

    this.dashboardSetupService.BMGFSaveOnlineDashboard(pobjJson, this.msFormMode).subscribe(
      res => {
        this.mbIsLoading = false;
        if (res.Response[0].ErrorCode == "") {
          this.mbChange = false;
          if (this.msFormMode == "I") {
            this.msFormMode = "U";
            this.msCopyRecord = "N";
            this.msDashboardKey = this.mObjModel.DashboardKey;
            this.objToastService.notifySuccess(this.objUtils.bmgfonTranslate("COMMON.MSG.SAVE"));
          } else if (this.msFormMode == "U") {
            this.objToastService.notifySuccess(this.objUtils.bmgfonTranslate("COMMON.MSG.EDIT"));
          }
          if (this.mbIsSearchMode) {
            this.mbIsSearchMode = false;
            this.bmlfOpenLookupModal('DashboardSetupList', this.msScreenName, 'DashboardSetupList', 'click', '', 'custom', 'cmdSearch', 'DASHBOARDSETUP.TITLE');
          } else if (this.mbIsAddMode) {
            this.mbIsAddMode = false;
            this.bmlfRefreshForm();
          } else if (this.mbIsCopyMode) {
            this.mbIsCopyMode = false;
            this.bmlfCopyData();
          } else if (this.mbIsCloseMode) {
            this.mbIsCloseMode = false;
            this.router.navigate(["/main/configuration/dashboard"]);
          } else {
            this.bmlfonDataonLoad();
          }
        } else {
          this.mbIsSearchMode = false;
          this.mbIsAddMode = false;
          this.mbIsCopyMode = false;
          this.mbIsDeleteMode = false;
          this.mbIsCloseMode = false;
          this.objToastService.notifyError(res.Response[0].ResponseText);
        }
        console.log(res);
      }
    ).add(() => { this.mbIsLoading = false; })
  }

