  // So Footer Action finction

  bmlfonClose() {
    if (this.mbChange == true) {
      this.mbSaveDialogView = true;
      this.mbIsCloseMode = true;
    } else {
      this.router.navigate(["/main/configuration/dashboard"]);
    }
  }
  bmlfFooterActionFn(event) {
    if (event == 'searchEvn') {
      if (this.mbChange) {
        this.mbSaveDialogView = true;
        this.mbIsSearchMode = true
        return false;
      }
      this.bmlfOpenLookupModal('DashboardSetupList', this.msScreenName, 'DashboardSetupList', 'click', '', 'custom', 'cmdSearch', 'DASHBOARDSETUP.TITLE');
    } else if (event == 'addEvn') {
      if (!this.objUtils.mbWrite) {
        this.objToastService.notifyWarning(this.objUtils.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
        return false;
      };
      if (!this.bmlfValidData()) {
        return;
      }
      if (this.mbChange) {
        this.mbSaveDialogView = true;
        this.mbIsAddMode = true
        return false;
      }
      this.bmlfRefreshForm();
    } else if (event == 'deleteEvn') {
      if (!this.objUtils.mbDelete) {
        this.objToastService.notifyWarning(this.objUtils.bmgfonTranslate('COMMON.PERMISSION_DELETE'));
        return false;
      };
      if (this.mObjModel.DashboardKey == '' && this.msFormMode == "I" && this.msCopyRecord == "N" && !this.mbChange) {
        this.objToastService.notifyWarning(this.objUtils.bmgfonTranslate('COMMON.MSG.NODATADELETE'));
        return false;
      }
      if (this.mObjModel.DashboardKey == '' && this.msFormMode == "I" && this.msCopyRecord == "Y" && !this.mbChange) {
        this.bmlfRefreshForm();
        return false;
      }
      this.mbShowDeleteDialog = true;
    } else if (event == 'copyEvn') {
      if (!this.objUtils.mbWrite) {
        this.objToastService.notifyWarning(this.objUtils.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
        return false;
      };
      if (CustomValidator.bmgfNullCheckValidator(this.mObjModel.DashboardKey)) {
        this.objToastService.notifyWarning(this.objUtils.bmgfonTranslate("COMMON.MSG.COPY"));
        document.getElementById("txtDashboardKey").focus();
        return;
      }
      if (this.mbChange) {
        this.mbSaveDialogView = true;
        this.mbIsCopyMode = true;
        return false;
      }
      this.bmlfCopyData();
    }
  }
  bmlfOnConfirmModal(psype) {
    if (psype == 'YES') {
      this.bmlfSubmitData();
      this.mbSaveDialogView = false;
    } else if (psype == 'NO') {
      this.mbSaveDialogView = false;
      if (this.mbIsSearchMode) {
        this.mbIsSearchMode = false;
        this.bmlfOpenLookupModal('DashboardSetupList', this.msScreenName, 'DashboardSetupList', 'click', '', 'custom', 'cmdSearch', 'DASHBOARDSETUP.TITLE');
      } else if (this.mbIsAddMode) {
        this.mbIsAddMode = false;
        this.bmlfRefreshForm();
      } else if (this.mbIsCopyMode) {
        this.mbIsCopyMode = false;
        this.bmlfCopyData();
      } else if (this.mbIsDeleteMode) {
        this.mbIsDeleteMode = false;
        this.mbShowDeleteDialog = true;
      }
      else {
        this.router.navigate(['/main/configuration/dashboard']);
      }
    } else {
      this.mbSaveDialogView = false;
      this.mbIsSearchMode = false;
      this.mbIsCopyMode = false;
      this.mbIsAddMode = false;
      this.mbIsCloseMode = false;
    }
  }
  bmlfonDialogAction(psAction) {
    if (psAction == 'No') {
      this.mbShowDeleteDialog = false;
    } else if (psAction == 'Yes') {
      if (this.msFormMode == "I") {
        this.bmlfRefreshForm();
        return false;
      }
      let parrHeaderData = [];
      parrHeaderData = [{
        "DashboardKey": this.mObjModel.DashboardKey.trim()
      }]
      let reqData: any = {
        JSON: JSON.stringify({
          FrcstH: parrHeaderData,
          Response: [{
            "ResponseText": "", "ErrorCode": ""
          }]
        })
      };
      this.dashboardSetupService.BMGFDeleteOnlineDashboard(reqData).subscribe(res => {
        if (res == null) {
          this.objToastService.notifyError(this.objUtils.bmgfonTranslate('COMMON.MSG.SERVERERROR'));
          return false;
        }
        if (res.Response[0].ErrorCode == "") {
          {
            if (res.Response[0].ResponseText.search("Reference") != -1) {
              this.objToastService.notifyError(this.objUtils.bmgfonTranslate('COMMON.MSG.EXIST_REF'));
              this.mbShowDeleteDialog = false;
              return;
            }
            this.objToastService.notifySuccess(this.objUtils.bmgfonTranslate('COMMON.MSG.DELETE'));
            this.mbShowDeleteDialog = false;
            this.bmlfRefreshForm();
          }
        } else {
          this.objToastService.notifyError(res.Response[0].ResponseText);
          this.mbShowDeleteDialog = false;
        }
      })
    }
  }
  bmlfOpenSelectedRecord(data) {
    let param = {
      DashboardKey: data.DashboardKey
    };
    this.objUtils.bmgfRefreshReuse('/main/configuration/dashboard/manage', true, param);
  }
  // Refresh Sales Order
  bmlfRefreshForm() {
    this.objUtils.bmgfRefreshReuse('/main/configuration/dashboard/manage', false);
  }
  bmlfCopyData() {
    let pobjHeaderData = Object.assign({}, this.mObjModel);
    pobjHeaderData.DashboardKey = "";
    let parrDetailData = [];
    let parrCopyData = {
      HeaderData: pobjHeaderData,
      DashKey: this.mObjModel.DashboardKey,
    }
    localStorage.setItem('copyDashboardSetupData', JSON.stringify(parrCopyData));
    this.bmlfRefreshForm();
  }

