import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';
import { Observable } from 'rxjs';
import { ODataModel } from 'src/app/core/models/odata.model';

@Injectable({
  providedIn: 'root'
})
export class DashboardSetupService {

  constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) { }

  public bmlfonGetDashboardData(headerData: any): Observable<ODataModel> {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetLookupData`;
    return this.httpExt.post<ODataModel>(url, headerData, null);
  }

  public BMGFGetOnlineDashboard(headerData: any): Observable<any> {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetOnlineDashboard`;
    return this.httpExt.post<any>(url, headerData, null);
  }

  public BMGFSaveOnlineDashboard(reqData, type): Observable<any> {
    if (type == "I") {
      const url = `${this.appSettings.ServerAPIEndpointnew}/bme/BMGFCreateOnlineDashboard`;
      return this.httpExt.post<any>(url, reqData, null);
    } else {
      const url = `${this.appSettings.ServerAPIEndpointnew}/bme/BMGFUpdateOnlineDashboard`;
      return this.httpExt.post<any>(url, reqData, null)
    }
  }
  public BMGFUpdateOnlineDashboard(reqData): Observable<any> {
    const url = `${this.appSettings.ServerAPIEndpointnew}/bme/BMGFUpdateOnlineDashboard`;
    return this.httpExt.post<any>(url, reqData, null)
  }
  public BMGFDeleteOnlineDashboard(reqData): Observable<any> {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFDeleteOnlineDashboard`;
    return this.httpExt.post<any>(url, reqData, null)
  }
  public BMGFGetQBScreenFields(reqData): Observable<any> {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetQBScreenFields`;
    return this.httpExt.post<any>(url, reqData, null)
  }
  public BMGFExeDataView(headerData: any) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/GETCustomSqlData`;
    return this.httpExt.post<any>(url, headerData, null);
  }
}
