import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';

@Injectable({
    providedIn: 'root'
  })
export class DefaultAccountSetupService {

    constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) {
    }
   
    public bmlfSaveDefaultAccountSetup(headerData: any, formMode) {
        if (formMode == "I") {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFSaveDefAcctSetup`;
            return this.httpExt.post<any>(url, headerData, null);
        }
    }

    public bmlfGetDefaultAccountSetup(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetDefAcctSetup`;
        return this.httpExt.post<any>(url, headerData, null);
    }
}
