import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';

@Injectable({
    providedIn: 'root'
  })
export class DocumentAuthorizationService {

    constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) {
    }
    public bmlfDeleteDocumentAuthorizationt(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/DeleteDocumentAuthorization`;
        return this.httpExt.post<any>(url, headerData, null);
    }

    public bmlfDeleteLineDocumentAuthorization(headerData: any) {
      const url = `${this.appSettings.ServerAPIEndpoint}/bme/DeleteLineDocumentAuthorization`;
      return this.httpExt.post<any>(url, headerData, null);
    }
    
    public bmlfSaveDocumentAuthorization(headerData: any, formMode) {
        if (formMode == "I") {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/CreateDocumentAuthorization`;
            return this.httpExt.post<any>(url, headerData, null);
        } else {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/UpdateDocumentAuthorization`;
            return this.httpExt.post<any>(url, headerData, null);
        }
    }

    public bmlfGetDocumentAuthorization(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetDocumentAuthorization`;
        return this.httpExt.post<any>(url, headerData, null);
    }
}