        this.mobjEmailLayoutModel.CUSTOM5 = custom.CUSTOM5 ? custom.CUSTOM5 : "";
        this.mobjEmailLayoutModel.CUSTOM6 = custom.CUSTOM6 ? custom.CUSTOM6 : "";
        this.mobjEmailLayoutModel.CUSTOM7 = custom.CUSTOM7 ? custom.CUSTOM7 : "";
        this.mobjEmailLayoutModel.CUSTOM8 = custom.CUSTOM8 ? custom.CUSTOM8 : "";
        this.mobjEmailLayoutModel.CUSTOM9 = custom.CUSTOM9 ? custom.CUSTOM9 : "";
