    ngOnInit() {
        /*--------- get screen permission --------*/
        this.utilService.getBMEScreePermission(this.msScreenName);

        /**--- Get default date format from service--- */
        this.msCustomDateFormat = this.dateFormatService.getDateFormat();
        this.mobjUserData = this.lsService.getUserSession();
        let psPrdKey = this.route.snapshot.queryParamMap.get('docKey');
        let msCopyRecord = this.route.snapshot.queryParamMap.get('CopyRecord');
        if (CustomValidator.bmgfNullCheckValidator(psPrdKey)) {
            this.msFormMode = "I";
            this.mbCustomControl = true; // Customization
            this.msPrdKey = "";
            this.mskeyValue = this.msPrdKey;
            let pobjCopyData = localStorage.getItem('copyProductData');
            if (pobjCopyData) {
                let pobjGetCopyData: any = JSON.parse(pobjCopyData);
                this.mobjDocumentAuthorizationModel = Object.assign({}, pobjGetCopyData.HeaderData);
                this.mobjDocumentAuthorizationModel.RecDate =new Date()
                this.marrDefoultValueGridData =  pobjGetCopyData.parrDetailsData;
                this.marrDefoultValueGridData.forEach(ele => {
                    ele.Flag = "A", ele.RecDate =new Date()
                })
                this.msCopyRecord = "Y";
                this.mskeyValue = pobjGetCopyData.docKey;
                localStorage.removeItem('copyProductData');
            }
            setTimeout(() => {
                document.getElementById("txtDesGrpKey").focus();
            }, 200);
        } else {
            this.msCopyRecord = CustomValidator.bmgfNullCheckValidator(msCopyRecord) ? "N" : msCopyRecord;
            /**----customization----- */

            this.msFormMode = this.msCopyRecord == "Y" ? "I" : "U";
            this.msPrdKey = psPrdKey;
            this.mskeyValue = this.msPrdKey;
            this.bmlfGetProductData();
        }
        this.mobjSignatureData = {
            scrrenName: this.msScreenName,
            reasonMinLength: 10,
            reqSignAuth: 2,
            date: moment().format(this.msCustomDateFormat.toUpperCase()),
            userName: this.mobjUserData.UserId
        };
        this.bmlfCheckEsignApplicableOnForm();
    }



    
/**--------Start: Delete Functionality----- */
bmlfonDeleteLineItem(rowIndex) {
    if(!this.utilService.mbDelete) {
      this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_DELETE'));
      return;
    };

    if(this.marrDefoultValueGridData.length == 1){
        this.mbdeleteLineItemWithHeader = true;
    }else{
        this.mbdeleteLineItem = true;
    }
   this.miGridDeleteLineIndex = rowIndex;
}


bmlfdialogDeleteLineActionWithHeader(PsType){
    this.bmlfonDialogAction(PsType) 
}





/**-------- Delete Functional----- */
bmlfdialogDeleteLineAction(action: any) {
  if(action == 'Yes') {
    this.mbdeleteLineItem = false;
    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{
            DocumentGroup: this.mobjDocumentAuthorizationModel.DocumentGroup,
            DocUserID: this.marrDefoultValueGridData[this.miGridDeleteLineIndex].DocUserId,
        }],
        Response: [{
          "ResponseText": "",
          "ErrorCode": ""
        }]
      })
    };
    this.formService.bmlfDeleteLineDocumentAuthorization(reqData).subscribe(res => {
      if(res == null) {
        return;
      }
      if(res.Response[0].ErrorCode != "") {
        this.toastMessageService.notifyError(res.Response[0].ResponseText);
      } else {
        this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate('COMMON.MSG.LINEDELETE'));
        this.marrDefoultValueGridData.splice(this.miGridDeleteLineIndex, 1);
        this.marrDefoultValueGridData.forEach((e, i) => e.Sequence = i + 1) // For Update Seq No...
      }
    });
  }
  if(action == 'No') {
    this.mbdeleteLineItem = false;
  }
}
/**--------close: Delete Functionality----- */



/** ---Degoult value array valication for insert line--- */
bmlfValidItemData() {
    let RowNumbers: number = 0;
    let pbResult: boolean = true;
    this.marrDefoultValueGridData.forEach(ele => {
      if(pbResult == true) {
        RowNumbers = this.marrDefoultValueGridData.slice(-1)[0].Sequence
        if(ele.DocUserId == '') {
          this.toastMessageService.notifyError((this.utilService.bmgfonTranslate('DOCUMENTAUTHORIZATION.MSG.USERID')) + ': Sr. No. ' + RowNumbers);
          pbResult = false;
        }
      }
    });
    return pbResult;
  }



    /** -- Add New row for defoult value --- */
bmlfAddItemData() {
    if(!this.mobjDocumentAuthorizationModel.DocumentGroup){
        this.bmlfOpenErrorBlock('DOCUMENTAUTHORIZATION.MSG.DOCGRPREQ');
        return false;
    }
    var Sequence=0;
    if(this.marrDefoultValueGridData.length ==0){
        Sequence=1
    }else{
        Sequence=this.marrDefoultValueGridData.slice(-1)[0].Sequence+1
    }
  
 if(this.bmlfValidItemData()) {
    let pobjData = new DocumentAuthorizationAddModel();
    pobjData.Sequence= Sequence;
    pobjData.DocumentGroup=this.mobjDocumentAuthorizationModel.DocumentGroup
    pobjData.DocUserId = '';
    pobjData.Name = ''
    pobjData.RecUserID = '';
    pobjData.Email = '';
    pobjData.DocRead = false;
    pobjData.DocWrite = false;
    pobjData.DocDelete = false;
    pobjData.Flag = "A";
    pobjData.RecDate = new Date();
    this.marrDefoultValueGridData.push(Object.assign({}, pobjData));
  }
}

toggle(dataItem: any, field: string){
    if(dataItem[field] ==  false){
        dataItem[field] =  true;
    }else{
        dataItem[field] =  false;
    }
}

    bmlfGetProductData() {
        this.mbIsLoading = true;
        let pobjOData = { Parameter: [], Response: [] };
        pobjOData.Parameter.push({
            "DocumentGroup": this.msPrdKey
        });
        pobjOData.Response.push({
            "ResponseText": "",
            "ErrorCode": ""
        })

        let psJData = JSON.stringify(pobjOData);
        let psTempjData = JSON.stringify(psJData);
        let psJson = '{"JSON":' + psTempjData + '}';
        this.formService.bmlfGetDocumentAuthorization(psJson).subscribe(
            res => {
                this.mbIsLoading = false;
                if (res != null && res.HeaderData.length > 0) {
                    this.mbCustomControl = true;
                    this.mobjDocumentAuthorizationModel = Object.assign({}, res.HeaderData[0]);
                    this.marrDefoultValueGridData= res.DetailData
                    if (this.msCopyRecord == "Y") {
                        this.mobjDocumentAuthorizationModel.DocumentGroup = "";
                        document.getElementById("txtPrdKey").focus();
                    }
                }
                this.bmlfCheckEsignApplicableOnForm();
            }
        ).add(() => {
            this.mbIsLoading = false;
        })
    }
