import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';

@Injectable({
  providedIn: 'root'
})
export class ReportScheduleSetupService {

  constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) { }
  public bmlfDeleteReportScheduleSetup(headerData: any) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/DeleteReportScheduleSetup`;
    return this.httpExt.post<any>(url, headerData, null);
}
public bmlfSaveReportScheduleSetup(headerData: any, formMode) {
    if (formMode == "I") {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/CreateReportScheduleSetup`;
        return this.httpExt.post<any>(url, headerData, null);
    } else {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/UpdateReportScheduleSetup`;
        return this.httpExt.post<any>(url, headerData, null);
    }
}

public bmlfGetReportScheduleSetup(headerData: any) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetReportScheduleSetup`;
    return this.httpExt.post<any>(url, headerData, null);
}
public bmlfGetReportParameter(headerData: any) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetReportParameter`;
    return this.httpExt.post<any>(url, headerData, null);
}

}
