import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';

@Injectable({
    providedIn: 'root'
  })
export class AlertSetupService {

    constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) {
    }
    public bmlfDeleteAlertSetup(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/DeleteAlertSetup`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfSaveAlertSetup(headerData: any, formMode) {
        if (formMode == "I") {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/CreateAlertSetup`;
            return this.httpExt.post<any>(url, headerData, null);
        } else {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/UpdateAlertSetup`;
            return this.httpExt.post<any>(url, headerData, null);
        }
    }

    public bmlfGetAlertSetup(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetAlertSetup`;
        return this.httpExt.post<any>(url, headerData, null);
    }

    public bmlfGetFieldsFromSQL(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetFieldsFromSQL`;
        return this.httpExt.post<any>(url, headerData, null);
    }

    public bmlfGetScreenkey(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetScreenkey`;
        return this.httpExt.post<any>(url, headerData, null);
    }

    
}
