import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';

@Injectable({
    providedIn: 'root'
  })
export class SopConfiguratorService {

    constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) {
    }
    public bmlfDeleteSOPConfigurator(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFDeleteSOPConfigurator`;
        return this.httpExt.post<any>(url, headerData, null);
    }

    public bmlfUpdateSOPConfigurator(headerData: any) {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFUpdateSOPConfigurator`;
            return this.httpExt.post<any>(url, headerData, null);
    }

    public bmlGetSOPConfigurator(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetSOPConfigurator`;
        return this.httpExt.post<any>(url, headerData, null);
    }

    public bmlfOnUploadDocument(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/UploadDocument`
        return this.httpExt.post<any>(url, headerData, { Anonymous: '' });
    }

    public bmlfViewSOPConfiguratorFile(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFViewSOPConfiguratorFile`
        return this.httpExt.post<any>(url, headerData, { Anonymous: '' });
    }
}