import { Component, OnInit, ViewChild } from '@angular/core';
import { UserFieldService } from 'src/app/shared/http/userfield.service';
// import 'rxjs/add/operator/filter';
import { Router } from '@angular/router';
import { LookupService } from 'src/app/shared/http/lookup.service';
import { ModalService } from 'src/app/core/services/modal.service';
import * as moment from 'moment';
import { ToastMessageService } from 'src/app/core/services/toast-message.service';
import { UOMConfiguration } from '../../models/uom-configuration';
import { SelectableSettings } from '@progress/kendo-angular-grid';
import { DateFormatService } from 'src/app/core/services/dateFormat.service';
import { BaseGrid } from 'src/app/shared/components/basegrid';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { AppUtility } from 'src/app/core/services/utility.services';
import { CustomValidator } from 'src/app/core/services/custom.validators';
@Component({
  selector: 'app-unit-of-measure-configuration',
  templateUrl: './unit-of-measure-configuration.component.html'
})
export class UnitOfMeasureConfigurationComponent  extends BaseGrid implements OnInit  {
  public getDataList(): void {}
  public gridDataList: any;
  public mobjUserData: any = {};
  public mbLoaderEnabled:boolean = true;
  public mobjFormData: UOMConfiguration = new UOMConfiguration();
  public mbIsLookupModal: boolean = false;
  public mobjLookupKeyData: any = {};
  public marrLookupGridData: any = [];
  public mbIsLoading: boolean = false;
  public mbEditMode = false;
  public mobjSelectableSettings: SelectableSettings;
  public mbCheckboxOnly = false;
  public msCustomDateFormat: string = "";
  public mbIsSaveDone: boolean = false;
  public mbSaveDialogView: boolean = false;
  public mbIsChange: boolean = false;
  public mbIsLookupFocus: boolean = false;
  public mbIsDeleteOrdMode: boolean = false;
  public mbDeleteOrdDialogView: boolean = false;
  public msLookupField:any = '';
  public marrDrillDownList: any = [];
  public mbIsHyerLinkRender: boolean = false;
  public mbHyerLinkData: any = null;
  public msScreenName:string = 'UOM Configuration';
  public mbSignatureModal: boolean = false;
  public mobjSignatureData: any = {};
  public mbCommonAlertDialog: boolean = false;
  public mobjCommonAlertData: any = {};
  public mbIsEsignature: boolean = false;
  public msKeyword = '';
  public mskeyValue:any = '';
  public mbMutiSelectionLookup = true;
  //Begin --- For Header Customization
  public mbCustomControl = false; // Custom Controls
  @ViewChild('custom') customCtrl: any;
  public mbConfirmDialogView:boolean = false;
  //End ---- For Customization
  @ViewChild('FieldExtender') FieldExtender : any ; //FieldExtender
  public customFields = ['CUSTOM1', 'CUSTOM2', 'CUSTOM3', 'CUSTOM4', 'CUSTOM5', 'CUSTOM6', 'CUSTOM7', 'CUSTOM8', 'CUSTOM9', 'CUSTOM10', 'USER1', 'USER2', 'USER3', 'USER4', 'USER5', 'USER6', 'USER7', 'USER8', 'USER9', 'USER10', 'USER11', 'USER12'];
  public mbIsTaskModal: boolean = false;
  public mobjTaskData: any = {};
 
  constructor(
    private lookupService: LookupService,
    private modalservice: ModalService,
    public dateFormatService: DateFormatService,
    private lsService: LocalStorageService,
    public router: Router,
    public utilService: AppUtility,
    public toastMessageService: ToastMessageService,
    public userFieldService: UserFieldService
  ) {
    super(lsService, dateFormatService, userFieldService, toastMessageService, utilService);
    this.bmlfGetDrillDownList();
  }

  /**
   * On Init Component
   */
  ngOnInit() {
    // uncomment later on when link is generated
    this.utilService.getBMEScreePermission(this.msScreenName);
    this.mobjUserData = this.lsService.getUserSession();
    // -- End
    this.bmlfGetDetails();
    this.mobjSignatureData = {
      scrrenName: this.msScreenName,
      reasonMinLength: 10,
      reqSignAuth: 2,
      date: moment().format(this.msCustomDateFormat.toUpperCase()),
      userName: this.mobjUserData.UserId
    };
    this.bmlfCheckEsignApplicableOnForm();
  }

  // Close Oreder Modal
  bmlfCloseForm(type) {
    debugger
    if (this.customCtrl.mbChange || this.FieldExtender.mbChange) {
      this.mbIsChange = true;
    }
    if (this.mbIsChange) {
      this.mbSaveDialogView = true;
      return false;
    }
    this.router.navigate(['/main/dashboard/dashboardpage']);
  }


  /**
   * On Order Action
   */
  bmlfConfirmSaveDetail(){
    this.mbSaveDialogView = true
  }

  bmlfOnSaveDetails(confirmFlag) {
    
    
    if (this.mbIsChange == false) {
      return;
    }
    let isMendentFileds = this.bmlfMendentFiledsMsg();
    if (isMendentFileds) {
      return false;
    }
   if(confirmFlag == true){
    this.mbConfirmDialogView = true;
    return
   }
    if (!this.utilService.mbWrite){
      this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('UNITOFMEASURECONFIGURATION.MESSAGE.PERMISSION_WRITE'));
      return false;
    }

    if (this.customCtrl.mbChange || this.FieldExtender.mbChange) {
      this.mbIsSaveDone = false;
      this.mbIsChange = true;
    }

    //Validate Field Extender -- Start
    let resultString = this.FieldExtender.bmgfValidationCheck();
    if (resultString.value == false){
      this.toastMessageService.notifyWarning(resultString.msg);
      return;
    }

    if (this.mbIsSaveDone) {
      return false;
    }
    this.mbIsChange = false;
    let custom = this.utilService.bmgfCustomModelTransform(this.customCtrl);
    this.bmlfonAssignCustValues(custom);


    let pobjFormDataCopy = Object.assign({}, this.mobjFormData);

    let pobjFormObj: any = {
      HeaderData: [],
      Response: [{ ResponseText: '', 'ErrorCode': '' }]
    }
    pobjFormObj.HeaderData.push(pobjFormDataCopy);
    if (this.mbIsEsignature) {
      this.bmlfopenESignatureModal();
      return false;
    }
    this.mbIsLoading = true;
    let pobjReqData: any = {
      ScreenName :'UOM Configuration',
      LookUpKey : 'InsertUOMConfig',
      Filter1:this.mobjFormData.WtUOM,
      Filter2:this.mobjFormData.VolUOM
    }; 
      this.bmlfDeleteUOM(pobjReqData);

  }

  bmlfDeleteUOM(pobjData){
    var psFlag = false;
    let pobjReqDataDelete: any = 
        {
          ScreenName :'UOM Configuration',
          LookUpKey : 'DeleteUOMConfig'
    };
    this.userFieldService.GETGeneralSqlData(pobjReqDataDelete).subscribe(parrRes => {
      console.log("paar res", parrRes.Response);
      if(parrRes.Response.length > 0){
        debugger
        if(parrRes.Response[0].ResponseText == 'Success') this.bmlfOnSaveFormDetails(pobjData);
        else psFlag = false;
      }
     
    })
    
  }

  bmlfOnSaveFormDetails(pobjData) {
    this.userFieldService.GETGeneralSqlData(pobjData).subscribe(parrRes => {
      console.log("paardata......", parrRes.Response[0])
      this.mbIsLoading = false;
      if (parrRes == null) {
        this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('UNITOFMEASURECONFIGURATION.MESSAGE.SERVER_ERROR_MSG'));
        return false;
      }

      if (parrRes.Response[0].ErrorCode == '') {
        debugger
        if (this.mbHyerLinkData != null) {
          this.bmlfRedirectFunctionality();
        }
        if (parrRes.Response[0].ErrorCode == '') {
          debugger
          this.mbSaveDialogView = false;
          this.mbIsSaveDone = true;
          this.mbEditMode = true;
          this.customCtrl.mbChange= false;
          this.FieldExtender.mbChange = false;
          this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate('COMMON.MSG.SAVE'));
          this.bmlfGetDetails();
          // FieldExtender start
       //   let screenFieldKeyValue = this.msScreenName + " — " +this.mobjFormData.Vendor_Key;
          let screenFieldKeyValue = this.msScreenName + " — ";
          this.bmlfSaveFieldExtender(screenFieldKeyValue);
          // -- End
        }
      } else {
        this.toastMessageService.notifyError(parrRes.Response[0].ResponseText);
      }
    }, err => {
      this.mbIsLoading = false;
      this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('UNITOFMEASURECONFIGURATION.MESSAGE.SERVER_ERROR_MSG'));
    });
  }

  bmlfSaveConfirmationModal(psype){
    if (psype == 'yes') {
      this.bmlfOnSaveDetails(false);
      this.mbConfirmDialogView = false;
    } else if (psype == 'no') {
      this.mbConfirmDialogView = false;
      this.router.navigate(['/main']);
    } else {
      this.mbConfirmDialogView = false;
    }
  }

  bmlfOnFromConfirmModal(psype) {
    if (psype == 'yes') {
      this.mbConfirmDialogView = true;
   //   this.bmlfOnSaveDetails();
      this.mbSaveDialogView = false;
    } else if (psype == 'no') {
      this.mbSaveDialogView = false;
      if (this.mbIsDeleteOrdMode) {
        this.mbIsDeleteOrdMode = false;
        this.bmlfRefreshForm();
      }
      else {
        this.router.navigate(['/main']);
      }
    } else {
      this.mbSaveDialogView = false;
    }
  }



  //Open LOOKUP MODAL
  bmlfonOpenLookupModal(psField, psEvent, psValue, psType, psEleId, psTitle="UNITOFMEASURECONFIGURATION.LOOKUPTITLE", piRowIdx = null) {
    if (psEvent == 'blur' && this.msLookupField == psValue) {
      this.msLookupField = '';
      return false;
    }

    this.mbMutiSelectionLookup = false;
    this.marrLookupGridData = [];
    this.mobjLookupKeyData = {};

    if (psEvent == 'blur' && (psValue == undefined || psValue == '')) {
      this.mobjFormData[psField + 'Des'] = '';
      return false
    }
    let psLookUpKey = this.bmlfonSetLookupKey(psField, piRowIdx);
    let psScreenName = this.msScreenName;
    let pobjReqData: any = {
      ScreenName: psScreenName
    }

    pobjReqData.LookUpKey = psLookUpKey;
   // this.utilService.bmgfOnKeyInputDisable();
    this.mbIsLoading = true;
    this.lookupService.getLookup(pobjReqData).subscribe(res => {
      setTimeout(() => {
        this.utilService.bmgfOnKeyInputEnable();
        this.mbIsLoading = false;
      }, 500);

      if (res == null) {
        this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('UNITOFMEASURECONFIGURATION.MESSAGE.NO_DATA_MSG'));
        return false;
      }
      if (Object.entries(res).length > 0 && res.LookupData.length > 0) {
        res.LookupData.forEach((ele) => {
          ele.Recdate = moment(ele.Recdate).format('MM/DD/YY');
        })
        if (psLookUpKey == 'UOM') {
          this.marrLookupGridData = res.LookupData;
        } 
        let title = '';
          title = this.utilService.bmgfonTranslate(psTitle);
        
        this.mobjLookupKeyData.field = psField;
        //this.mobjLookupKeyData.title = title + ' Lookup';
        this.mobjLookupKeyData.title = title.replace('Lookup','') ;
        this.mobjLookupKeyData.gridData = this.marrLookupGridData;
        this.mobjLookupKeyData.type = psType;
        this.mobjLookupKeyData.eleId = psEleId;
        if (psValue) {
          this.mobjLookupKeyData.searchValue = psValue;
        } else {
          this.mobjLookupKeyData.searchValue = '';
        }
        if (psEvent == 'blur' && psValue) {
          let psInputKey = this.bmlfGetInputColumn(psField);
          let parrFilterData: any = [];
          parrFilterData = this.marrLookupGridData.filter(obj => {
            let tempValue = obj[psInputKey] == null || obj[psInputKey] == '' ? '' : obj[psInputKey].toUpperCase();
            if (psInputKey=='Lang'){
              if (tempValue.trim() == psValue.toUpperCase()) {
                return obj;
              }
            }else { 
              if (tempValue == psValue.toUpperCase()) {
                return obj;
              }
            }
          });

          if (parrFilterData.length == 1) {
            debugger
            this.mobjLookupKeyData.value = parrFilterData[0]
            this.bmlfonSaveLookupField(this.mobjLookupKeyData);
            this.mbIsLookupModal = false;
          } else if (parrFilterData.length == 0) {
              this.mobjFormData[psField] = '';
              let elementExists = document.getElementById(psEleId);
              if (elementExists) {
                debugger
                console.log("pseleid....", psEleId)
                document.getElementById(psEleId).focus();
              }
              this.mbIsLookupModal = true;
          } else {
            this.mbIsLookupModal = true;
          }
        } else if (psEvent == 'click') {
          this.mbIsLookupModal = true;
        }
      } else {
        this.mobjFormData[psField] = '';
        if (psEleId) {
          debugger
          document.getElementById(psEleId).focus();
        }
        this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('UNITOFMEASURECONFIGURATION.MESSAGE.NO_DATA_MSG'));
      }
    }, err => {
      console.log(err);
    });
  }

  //Close LOOKUP MODAL
  bmlfonCloseLookupModal(pobjData) {
    console.log("eleid in close", pobjData.eleId)
    debugger
    this.mbIsLookupModal = pobjData.isLookupModal;
    let psInputDesKey = this.bmlfGetInputDesColumn(pobjData.field);
    if (pobjData.eleId != null) {
      let psInputKey = this.bmlfGetInputColumn(pobjData.field);
      let parrFilterData: any = [];
      if (psInputKey) {
        parrFilterData = pobjData.gridData.filter(obj => obj[psInputKey].toUpperCase() == pobjData.searchValue ? pobjData.searchValue.toUpperCase() : '');
      }
      // console.log(pos);
      if (parrFilterData.length == 0) {
        this.mobjFormData[pobjData.field] = '';
        this.mobjFormData[pobjData.field + 'Des'] = '';
      }
        document.getElementById(pobjData.eleId).focus();
    }
  }

  bmlfonSaveLookupField(pobjData) {

    debugger
    let psInputKey = this.bmlfGetInputColumn(pobjData.field);
    let psInputDesKey = this.bmlfGetInputDesColumn(pobjData.field);
    this.mbIsLookupModal = false;
    this.mbMutiSelectionLookup = false;
    if (!Array.isArray(pobjData.value)) {
      if (pobjData.type == 'custom') {
        this.mobjFormData[pobjData.field] = pobjData.value[psInputKey];
        console.log("mobj data", pobjData.field)
        this.bmlfonGlobalOnChangesFn(pobjData.field, pobjData, null);
    //    this.bmlfonTransferValues(pobjData.field, pobjData.value, null, pobjData.eleId);
        if (psInputDesKey) {
          debugger
          this.mobjFormData[pobjData.field + 'Des'] = pobjData.value[psInputDesKey];
        }
      }
    } else {
   //   this.bmlfonTransferValues(pobjData.field, pobjData.value, undefined, undefined);
    }
   
  }

  // Set Lookup KEY
  bmlfonSetLookupKey(psField, piRowIdx) {
    debugger
    let psLookUpKey;
    if (psField == 'WtUOM' ) {
      return psLookUpKey = 'UOM';
    }
    else if (psField == 'VolUOM'){
      return psLookUpKey = 'UOM'
    }
    return psLookUpKey
 //   console.log("ps look up key", psLookUpKey) 
  }


  // get Lookup Value Column
  bmlfGetInputColumn(psField) {
    if (psField == 'WtUOM' ) {
      return 'Uomkey';
    }
    if (psField == 'VolUOM' ) {
      return 'Uomkey';
    }
  }

  // Set Lookup Description Column
  bmlfGetInputDesColumn(psField) {
    if (psField == 'WtUOM') {
      return 'Description'
    } 
    if (psField == 'VolUOM') {
      return 'Description'
    }
  }

  // Change Tab
  // bmlfonSwitchTab(psType) {
  //   if (psType !='first') {
  //     if (this.mobjFormData.Vendor_Key == '' || this.mobjFormData.Vendor_Key == undefined) {
  //       this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('PURCHASEMODULE.VALIVENDKEYMSG'));
  //       return false;
  //     }
  //   }
  //   this.msTabType = psType;
  // }

  // Open Modal
  bmlfonOpenModal(psId, piIndex = null) {
    this.modalservice.open(psId);
  }

  // Change Function
  bmlfonChange(psFieldType, psValue, psIsChange=null) {
  }

  bmlfonCloseModal(psId) {
    this.modalservice.close(psId);
    this.msKeyword = '';
  }

  // get selected so data
  bmlfGetDetails() {
    debugger
    this.mbIsLoading = true;
    let pobjReqData: any = {
          ScreenName :'UOM Configuration',
          LookUpKey : 'UOMConfig'
      };
      
   
    this.userFieldService.GETGeneralSqlData(pobjReqData).subscribe(res => {
      this.mbLoaderEnabled = false;
      console.log("response ......", res)
      this.mbIsLoading = false;
      if (res == null) {
        this.mbCustomControl = true;
        this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('UNITOFMEASURECONFIGURATION.MESSAGE.NO_DATA_MSG'));
        return false;
      }

      if (Object.entries(res.LookupData).length > 0) {
        this.mobjFormData = res.LookupData[0];

        //FiledExtender -- Start
     //  this.mskeyValue = this.mobjFormData.Vendor_Key;
        // -- End
       
        this.bmlfGetLookupDes(this.mobjFormData);
        this.mbIsChange = false;
        this.bmlfCheckEsignApplicableOnForm();
      }
      this.mbCustomControl = true;
    }, err => {
      this.mbIsLoading = false;
      console.log(err);
    })
  }

  bmlfonGlobalOnChangesFn(psFieldName, pobjFieldData, piIndex = null) {
    if (pobjFieldData == null) {
      return false;
    }
    this.mbIsChange = true;
    this.mbIsSaveDone = false;
  }
  // Global focus Fn
  bmlfOnGlobalFocus(field, value) {
    if (typeof value === 'object' && value.constructor === Object) {
      let valueCopy = Object.assign({}, value);
      this.msLookupField = valueCopy[field];
    } else {
      this.msLookupField = value;
    }
  }

  // Global Blur Fn
  bmlfOnGlobalBlur(field, value) {
    if (field == 'lookup') {
      this.mbIsLookupFocus = false;
    }
  }

  // Get Lookup Description
  bmlfGetLookupDes(pobjData) {
    debugger
    let pKeyCode = 'UNIT';
    let parrSetKeyData = [
        { KeyCode: pKeyCode, KeyValue1: pobjData.WtUOM, KeyValue2: 'WeightUOMKey', KeyDescription: '' },
        { KeyCode: pKeyCode, KeyValue1: pobjData.VolUOM, KeyValue2: 'VolumeUOMKey', KeyDescription: '' }
    ]
  
    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: parrSetKeyData,
        ScreenName:[{ ScreenName:this.msScreenName}],
        Response:[ { ResponseText:'', ErrorCode:''}]
      })
    };

    this.userFieldService.bmlfFetchKeyDescription(reqData).subscribe(res => {
      if (res == null) {
        return false;
      }
      if (res.LookupData.length) {
        res.LookupData.forEach((ele) => {
          if (ele.KeyValue2 == 'WeightUOMKey') {
            this.mobjFormData.WtUOMDes = ele.KeyDescription;
          }
         else if (ele.KeyValue2 == 'VolumeUOMKey') {
            this.mobjFormData.VolUOMDes = ele.KeyDescription;
          }
        });
      }
    }, err => {
      console.log(err);
    });
  }


  // Refresh Form
  bmlfRefreshForm() {
    this.utilService.bmgfRefreshReuse('/main/configuration/unit-of-measure-configuration', false);
  }



  // GetDrillDownList
  bmlfGetDrillDownList() {
    let pobjReqData = {
      ScreenName: 'ApplyPaymentComponent',
      LookUpKey: 'DrillDwnList',
      Filter1: this.msScreenName 
    }
    this.userFieldService.GETGeneralSqlData(pobjReqData).subscribe(res => {
      if (res.LookupData.length > 0) {
        this.marrDrillDownList = res.LookupData;
        this.mbIsHyerLinkRender = true;
      }
    });
  }

  // DrillDown emit event
  bmgfDrillDownEvent(pobjData: any) {
    this.mbHyerLinkData = pobjData;
    this.bmlfOnSaveDetails(false);
  }

  bmlfRedirectFunctionality() {
    if (!CustomValidator.bmgfNullCheckValidator(this.mbHyerLinkData.data.User1)) {
      let queryParamsObj = { queryParams: {} };
      queryParamsObj.queryParams[this.mbHyerLinkData.data.User2] = this.mbHyerLinkData.msField1Value;
      if (!CustomValidator.bmgfNullCheckValidator(this.mbHyerLinkData.data.KeyValue2)) {
        queryParamsObj.queryParams[this.mbHyerLinkData.data.User3] = this.mbHyerLinkData.msField2Value;
      }
      this.router.navigate([this.mbHyerLinkData.data.User1], queryParamsObj);
    }
  }

  // check e signature
  bmlfCheckEsignApplicableOnForm() {
    let pobjReqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{
          MenuScreenName: this.msScreenName,
        }],
        Response: [{
          ResponseText: '',
          ErrorCode: ''
        }]
      })
    };
    this.userFieldService.bmlfCheckEsignApplicable(pobjReqData).subscribe(res => {
      if (res == null) {
        return false;
      }
      if (res.Response[0].ErrorCode == '') {
        this.mbIsEsignature = res.ESignData[0].Permision;
        this.mobjSignatureData.reasonMinLength = res.ESignData[0].ReasonLength;
        this.mobjSignatureData.reqSignAuth = res.ESignData[0].NoOfReqSign;
      }
    }, err => {
      console.log(err);
    });
  }

  // open e signature modal
  bmlfopenESignatureModal() {
    this.mbSignatureModal = true;
    this.mobjSignatureData.isShow = true;
  //  this.mobjSignatureData.primaryKey = this.mobjFormData.Vendor_Key;
  }

  // check e signature
  bmlfChkESignature(pobjData) {
    this.mbSignatureModal = pobjData.isShow;
    if (pobjData.event == 'Save') {
      this.mbIsEsignature = false;
      this.mobjFormData.ESG_REASON = pobjData.changeReason;
      this.mobjFormData.ESG_APPROVER = pobjData.eSignApprover;
      this.mbIsChange = true;
      this.bmlfOnSaveDetails(false);
    } else if (pobjData.event == 'Cancel'){
      this.mobjCommonAlertData = {}
      this.mbCommonAlertDialog = true;
      this.mobjCommonAlertData.message = this.utilService.bmgfonTranslate('ESIGNREASONMSG');
      this.mobjCommonAlertData.event = 'close';
      this.mbIsChange = true;
    }
  }

  // add Custoom values for headers
  bmlfonAssignCustValues(custom) {
    this.mobjFormData.CUSTOM1 = custom.CUSTOM1 ? custom.CUSTOM1 : "";
    this.mobjFormData.CUSTOM2 = custom.CUSTOM2 ? custom.CUSTOM2 : "";
    this.mobjFormData.CUSTOM3 = custom.CUSTOM3 ? custom.CUSTOM3 : "";
    this.mobjFormData.CUSTOM4 = custom.CUSTOM4 ? custom.CUSTOM4 : "";
    this.mobjFormData.CUSTOM5 = custom.CUSTOM5 ? custom.CUSTOM5 : "";
    this.mobjFormData.CUSTOM6 = custom.CUSTOM6 ? custom.CUSTOM6 : "";
    this.mobjFormData.CUSTOM7 = custom.CUSTOM7 ? custom.CUSTOM7 : "";
    this.mobjFormData.CUSTOM8 = custom.CUSTOM8 ? custom.CUSTOM8 : "";
    this.mobjFormData.CUSTOM9 = custom.CUSTOM9 ? custom.CUSTOM9 : "";
    this.mobjFormData.CUSTOM10 = custom.CUSTOM10 ? custom.CUSTOM10 : "";

    this.mobjFormData.User1 = custom.User1 ? custom.User1 : "";
    this.mobjFormData.User2 = custom.User2 ? custom.User2 : "";
    this.mobjFormData.User3 = custom.User3 ? custom.User3 : "";
    this.mobjFormData.User4 = custom.User4 ? custom.User4 : "";
    this.mobjFormData.User5 = custom.User5 ? custom.User5 : "";
    this.mobjFormData.User6 = custom.User6 ? custom.User6 : "";
    this.mobjFormData.User7 = custom.User7 ? custom.User7 : "";
    this.mobjFormData.User8 = custom.User8 ? custom.User8 : "";
    this.mobjFormData.User9 = custom.User9 ? custom.User9 : "";
    this.mobjFormData.User10 = custom.User10 ? custom.User10 : "";
    this.mobjFormData.User11 = custom.User11 ? custom.User11 : "";
    this.mobjFormData.User12 = custom.User12 ? custom.User12 : "";
  }


  // fieldExtender save fn
  bmlfSaveFieldExtender(screenKeyValue) {
    // FieldExtender -- start
    let jsonData = this.FieldExtender.bmlfSaveData(screenKeyValue);
    if (jsonData.length > 0) {
      this.FieldExtender.bmlfCallApi(jsonData).then(res => {}).catch(err => {console.log(err);});
    }
  }

  bmlfMendentFiledsMsg(evenType?) {
    let isMendentFiled = false;
    if (this.mobjFormData.WtUOM == '' || this.mobjFormData.WtUOM == undefined) {
      let msg = this.utilService.bmgfonTranslate('UNITOFMEASURECONFIGURATION.MESSAGE.WEIGHTUOMBLANK');
     
      this.toastMessageService.notifyError(msg);
      isMendentFiled = true;
      return isMendentFiled;
    }
    if (this.mobjFormData.VolUOM == '' || this.mobjFormData.VolUOM == undefined) {
      let msg = this.utilService.bmgfonTranslate('UNITOFMEASURECONFIGURATION.MESSAGE.VOLUMEUOMBLANK');
      this.toastMessageService.notifyError(msg)
      isMendentFiled = true;
      return isMendentFiled;
    }
    
    return isMendentFiled;
  }

  //Open Task MODAL
  bmlfOpenTaskModal(taskType) {
    this.mobjTaskData = {};
    this.mobjTaskData.taskType = taskType;
    if (taskType == 'myTask') {
      this.mobjTaskData.title = this.utilService.bmgfonTranslate('MYTASK');
      this.mobjTaskData.innerRecord = this.mobjUserData;
    } else if (taskType == 'recordTask') {
      this.mobjTaskData.title = this.utilService.bmgfonTranslate('TASKFORRECORD');
      let pobjFormRec:any = {};
      pobjFormRec.formName = this.msScreenName;
      pobjFormRec.primeKey = this.mobjFormData.WtUOM;
      this.mobjTaskData.innerRecord = pobjFormRec;
    }
    this.mbIsTaskModal = true;
  }

  //Close Task MODAL
  bmlfonCloseTaskModal(pobjData) {
    this.mbIsTaskModal = pobjData.isTaskModal;
  }

  bmlfViewHelpDoc() {
    window.open('../../../../assets/help files/UOM Configuration.pdf', '_blank');
  }


  // end tcs 04/mar/2021 deepak
}
 


