import { Component, OnInit, ViewChild } from '@angular/core';
import { UserFieldService } from 'src/app/shared/http/userfield.service';
// import 'rxjs/add/operator/filter';
import { Router } from '@angular/router';
import { LookupService } from 'src/app/shared/http/lookup.service';
import { ModalService } from 'src/app/core/services/modal.service';
import * as moment from 'moment';
import { ToastMessageService } from 'src/app/core/services/toast-message.service';
import { SelectableSettings } from '@progress/kendo-angular-grid';
import { DateFormatService } from 'src/app/core/services/dateFormat.service';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { AppUtility } from 'src/app/core/services/utility.services';
import {DefaultAccountSetupService} from '../../http/default-account-setup.service';
import {DefaultAccountSetupModal} from '../../models/default-account-setup.modal';


@Component({
  selector: 'app-default-account-setup',
  templateUrl: './default-account-setup.component.html',
  styleUrls:['./default-account-setup.component.scss']
})
export class DefaultAccountSetupComponent implements OnInit {
  public mnRowIndex:any = null;
  public msFormMode = "I";
  public AccountData = [];
  public FilteredAccountData = [];
  mstabType:any = 'Customer Class Account';
  mstabTypeSmall:any = 'Customer Class';
  public mslookupScreenname:any = 'Account'
  public getDataList(): void {}
  public gridDataList: any;
  public mobjUserData: any = {};
  public mbLoaderEnabled:boolean = true;
  public mobjFormData: DefaultAccountSetupModal = new DefaultAccountSetupModal();
  public mbIsLookupModal: boolean = false;
  public mobjLookupKeyData: any = {};
  public marrLookupGridData: any = [];
  public mbIsLoading: boolean = false;
  public mbEditMode = false;
  public mobjSelectableSettings: SelectableSettings;
  public mbCheckboxOnly = false;
  public msCustomDateFormat: string = "";
  public mbIsSaveDone: boolean = false;
  public mbSaveDialogView: boolean = false;
  public mbIsChange: boolean = false;
  public mbIsLookupFocus: boolean = false;
  public mbIsDeleteOrdMode: boolean = false;
  public mbDeleteOrdDialogView: boolean = false;
  public msLookupField:any = '';
  public marrDrillDownList: any = [];
  public mbIsHyerLinkRender: boolean = false;
  public mbHyerLinkData: any = null;
  public msScreenName:string = 'UOM Configuration';
  public mbSignatureModal: boolean = false;
  public mobjSignatureData: any = {};
  public mbCommonAlertDialog: boolean = false;
  public mobjCommonAlertData: any = {};
  public mbIsEsignature: boolean = false;
  public msKeyword = '';
  public mskeyValue:any = '';
  public mbMutiSelectionLookup = true;
  //Begin --- For Header Customization
  public mbCustomControl = false; // Custom Controls
  public mbConfirmDialogView:boolean = false;
  public mbIsCloseMode: boolean = false;
  //End ---- For Customization
  @ViewChild('FieldExtender') FieldExtender : any ; //FieldExtender
  public customFields = ['CUSTOM1', 'CUSTOM2', 'CUSTOM3', 'CUSTOM4', 'CUSTOM5', 'CUSTOM6', 'CUSTOM7', 'CUSTOM8', 'CUSTOM9', 'CUSTOM10', 'USER1', 'USER2', 'USER3', 'USER4', 'USER5', 'USER6', 'USER7', 'USER8', 'USER9', 'USER10', 'USER11', 'USER12'];
  public mbIsTaskModal: boolean = false;
  public mobjTaskData: any = {};
  constructor(private lookupService: LookupService,
    private modalservice: ModalService,
    public dateFormatService: DateFormatService,
    private lsService: LocalStorageService,
    public router: Router,
    public utilService: AppUtility,
    public toastMessageService: ToastMessageService,
    public defaultAccountSetupService:DefaultAccountSetupService,
    public userFieldService: UserFieldService) { }

    ngOnInit() {
      // uncomment later on when link is generated
      this.utilService.getBMEScreePermission(this.msScreenName);
      this.mobjUserData = this.lsService.getUserSession();
      // -- End
      this.bmlfGetDetails();
     
    }
  
    // Close Oreder Modal
    bmlfCloseForm(type) {
      if (this.FieldExtender.mbChange) {
        this.mbIsChange = true;
      }
      if (this.mbIsChange) {
        this.mbSaveDialogView = true;
        this.mbIsCloseMode = true;
        return false;
      }
      this.router.navigate(['/main/dashboard/dashboardpage']);
    }
  
  
    /**
     * On Order Action
     */
    bmlfConfirmSaveDetail(){
      this.mbSaveDialogView = true
    }
  
    bmlfOnSaveDetails() {
      let isMendentFileds = this.bmlfMendentFiledsMsg();
      if (isMendentFileds) {
        return false;
      }
      if (!this.utilService.mbWrite){
        this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('UNITOFMEASURECONFIGURATION.MESSAGE.PERMISSION_WRITE'));
        return false;
      }

      if (this.mbIsChange == false) {
        return;
    }
  
      if (this.FieldExtender.mbChange) {
        this.mbIsSaveDone = false;
        this.mbIsChange = true;
      }
  
      //Validate Field Extender -- Start
      let resultString = this.FieldExtender.bmgfValidationCheck();
      if (resultString.value == false){
        this.toastMessageService.notifyWarning(resultString.msg);
        return;
      }
  
      if (this.mbIsSaveDone) {
        return false;
      }
      this.mbIsChange = false;
      let pobjFormDataCopy = Object.assign({}, this.mobjFormData);
  
      let pobjFormObj: any = {
        HeaderData: [],
        Response: [{ ResponseText: '', 'ErrorCode': '' }]
      }
      pobjFormObj.HeaderData.push(pobjFormDataCopy);
      if (this.mbIsEsignature) {
        return false;
      }
      this.mbIsLoading = true;
      this.bmlfOnSaveFormDetails();
  
    }
  
 
  
    bmlfOnSaveFormDetails() {
      this.mbIsLoading = true;
      let parrHeaderData = [];
       parrHeaderData = this.AccountData;
      
      //  setTimeout(()=>{
      //     parrHeaderData.forEach((e)=>{
      //    delete e.className;
      //    delete e.AccountDesc;
      //    delete e.AccountName;
      //   });
      //  }, 1000)
      
      let pobjJData = {
          HeaderData: parrHeaderData,
          Response: [{
              "ResponseText": "",
              "ErrorCode": ""
          }]
      };
      let pobjJson = {
          JSON:JSON.stringify(pobjJData)
      };

      this.defaultAccountSetupService.bmlfSaveDefaultAccountSetup(pobjJson, this.msFormMode).subscribe(parrRes=>{
      this.mbIsLoading = false;
        if (parrRes == null) {
          this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('COMMON.MESSAGE.SERVER_ERROR_MSG'));
          return false;
        }
  
        if (parrRes.Response[0].ErrorCode == '') {
          if (parrRes.Response[0].ErrorCode == '') {
            this.mbSaveDialogView = false;
            this.mbIsSaveDone = true;
            this.mbEditMode = true;
            this.FieldExtender.mbChange = false;
            this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate('COMMON.MSG.SAVE'));
            this.bmlfGetDetails();
            // FieldExtender start
         //   let screenFieldKeyValue = this.msScreenName + " — " +this.mobjFormData.Vendor_Key;
            let screenFieldKeyValue = this.msScreenName + " — ";
            this.bmlfSaveFieldExtender(screenFieldKeyValue);
            if (this.mbIsCloseMode) {
              this.mbIsCloseMode = false;
              this.router.navigate(["/main/dashboard/dashboardpage"]);
          }
            // -- End
          }
        } else {
          this.toastMessageService.notifyError(parrRes.Response[0].ResponseText);
        }
      }, err => {
        this.mbIsLoading = false;
        this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('COMMON.MESSAGE.SERVER_ERROR_MSG'));
      });
    }
  
    bmlfSaveConfirmationModal(psype){
      if (psype == 'yes') {
        this.bmlfOnSaveDetails();
        this.mbSaveDialogView = false;
      } else if (psype == 'no') {
        this.mbSaveDialogView = false;
        this.router.navigate(['/main']);
      } else {
        this.mbSaveDialogView = false;
        this.mbIsCloseMode = false;
      }
    }
    //Open LOOKUP MODAL
    bmlfonOpenLookupModal(psField, psEvent, psValue, psType, psEleId, psTitle, piRowIdx = null) {
     this.mnRowIndex = piRowIdx;
     psTitle="DEFAULTACCOUNTSETUP.ACCOUNT";
      if (psEvent == 'blur' && this.msLookupField == psValue) {
        this.msLookupField = '';
        return false;
      }
  
      this.mbMutiSelectionLookup = false;
      this.marrLookupGridData = [];
      this.mobjLookupKeyData = {};
  
      if (psEvent == 'blur' && (psValue == undefined || psValue == '')) {
        this.AccountData[piRowIdx][psField] = '';
        this.AccountData[piRowIdx]['Description'] = '';
        return false
      }
      let psLookUpKey = this.bmlfonSetLookupKey(psField, piRowIdx);
      let psScreenName = this.mslookupScreenname;
      let pobjReqData: any = {
        LookUpKey : psLookUpKey,
        ScreenName: psScreenName
      }

      this.mbIsLoading = true;
      this.lookupService.getLookup(pobjReqData).subscribe(res => {
        setTimeout(() => {
          this.utilService.bmgfOnKeyInputEnable();
          this.mbIsLoading = false;
        }, 500);
  
        if (res == null) {
          this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('UNITOFMEASURECONFIGURATION.MESSAGE.NO_DATA_MSG'));
          return false;
        }
        if (Object.entries(res).length > 0 && res.LookupData.length > 0) {
          res.LookupData.forEach((ele) => {
            if(ele.Recdate != null || ele.Recdate != undefined){
            ele.Recdate = moment(ele.Recdate).format('MM/DD/YY');
            }
          })
          
            this.marrLookupGridData = res.LookupData;
          
          let title = '';
            title = this.utilService.bmgfonTranslate(psTitle);
          
          this.mobjLookupKeyData.field = psField;
          //this.mobjLookupKeyData.title = title + ' Lookup';
          this.mobjLookupKeyData.title = title.replace('Lookup','') ;
          this.mobjLookupKeyData.gridData = this.marrLookupGridData;
          this.mobjLookupKeyData.type = psType;
          this.mobjLookupKeyData.eleId = psEleId;
          if (psValue) {
            this.mobjLookupKeyData.searchValue = psValue;
          } else {
            this.mobjLookupKeyData.searchValue = '';
          }
          if (psEvent == 'blur' && psValue) {
            let psInputKey = this.bmlfGetInputColumn(psField);
            let parrFilterData: any = [];
            parrFilterData = this.marrLookupGridData.filter(obj => {
              let tempValue = obj[psInputKey] == null || obj[psInputKey] == '' ? '' : obj[psInputKey].toUpperCase();
              if (psInputKey=='Lang'){
                if (tempValue.trim() == psValue.toUpperCase()) {
                  return obj;
                }
              }else { 
                if (tempValue == psValue.toUpperCase()) {
                  return obj;
                }
              }
            });
  
            if (parrFilterData.length == 1) {
              this.mobjLookupKeyData.value = parrFilterData[0];
              this.bmlfonSaveLookupField(this.mobjLookupKeyData);
              this.mbIsLookupModal = false;
            } else if (parrFilterData.length == 0) {
                this.mobjFormData[psField] = '';
                let elementExists = document.getElementById(psEleId);
                if (elementExists) {
                  document.getElementById(psEleId).focus();
                }
                this.mbIsLookupModal = true;
            } else {
              this.mbIsLookupModal = true;
            }
          } else if (psEvent == 'click') {
            this.mbIsLookupModal = true;
          }
        } else {
          this.mobjFormData[psField] = '';
          if (psEleId) {
            document.getElementById(psEleId).focus();
          }
          this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('UNITOFMEASURECONFIGURATION.MESSAGE.NO_DATA_MSG'));
        }
      }, err => {
        console.log(err);
      });
    }
  
    //Close LOOKUP MODAL
    bmlfonCloseLookupModal(pobjData) {
      this.mbIsLookupModal = pobjData.isLookupModal;
    //  let psInputDesKey = this.bmlfGetInputDesColumn(pobjData.field);
      if (pobjData.eleId != null) {
        let psInputKey = this.bmlfGetInputColumn(pobjData.field);
        let parrFilterData: any = [];
        //if (psInputKey) {
        // Bug 182335 - 28-11-23
        if (psInputKey && pobjData.searchValue != null) {
          parrFilterData = pobjData.gridData.filter(obj => obj[psInputKey].toUpperCase() == pobjData.searchValue.toUpperCase());
        }
        // console.log(pos);
        //if (parrFilterData.length == 0) {
          // Bug 182335 - 28-11-23
        if (parrFilterData.length == 0 || pobjData.searchValue == null) {
          this.AccountData[this.mnRowIndex][pobjData.field] = '';
          this.AccountData[this.mnRowIndex]['Description'] = '';
        }
          document.getElementById(pobjData.eleId).focus();
      }
    }
  
    bmlfonSaveLookupField(pobjData,piRowIdx=null) {
      let psInputKey = this.bmlfGetInputColumn(pobjData.field);
  //    let psInputDesKey = this.bmlfGetInputDesColumn(pobjData.field);
      this.mbIsLookupModal = false;
      this.mbMutiSelectionLookup = false;
      if (!Array.isArray(pobjData.value)) {
        if (pobjData.type == 'custom') {
          this.AccountData[this.mnRowIndex][pobjData.field] = pobjData.value[psInputKey];
          this.AccountData[this.mnRowIndex]['Description'] = pobjData.value['Description'];
       //   this.mobjFormData[pobjData.field] = pobjData.value[psInputKey];
          this.bmlfonGlobalOnChangesFn(pobjData.field, pobjData, null);
      //    this.bmlfonTransferValues(pobjData.field, pobjData.value, null, pobjData.eleId);
         
        }
      } else {
     //   this.bmlfonTransferValues(pobjData.field, pobjData.value, undefined, undefined);
      }
     
    }
  
    // Set Lookup KEY
    bmlfonSetLookupKey(psField, piRowIdx) {
      let psLookUpKey;
      psLookUpKey = 'Account';
      return psLookUpKey
   //   console.log("ps look up key", psLookUpKey) 
    }
  
  
    // get Lookup Value Column
    bmlfGetInputColumn(psField) {
      if (psField == 'DfltKey' ) {
        return 'Acct';
      }
    }
  
    // Set Lookup Description Column
    // bmlfGetInputDesColumn(psField) {
    //   if (psField == 'DfltKey') {
    //     return 'Description'
    //   } 
    //   if (psField == 'VolUOM') {
    //     return 'Description'
    //   }
    // }

    // Open Modal
    bmlfonOpenModal(psId, piIndex = null) {
      this.modalservice.open(psId);
    }
  
    
    bmlfonCloseModal(psId) {
      this.modalservice.close(psId);
      this.msKeyword = '';
    }
  
    // get selected so data
    bmlfGetDetails() {
      let pobjJData = {
        HeaderData: [{}],
        Response: [{
            "ResponseText": "",
            "ErrorCode": ""
        }]
    };
    let pobjJson = {
        JSON:JSON.stringify(pobjJData)
    };
      this.defaultAccountSetupService.bmlfGetDefaultAccountSetup(pobjJson).subscribe(res=>{
        this.mbLoaderEnabled = false;
        this.AccountData = res.HeaderData;
        this.AccountData.forEach(ele=>{
          let temp;
          let dfltName = ele.DfltName;
          if(dfltName.indexOf('Number') > -1){
             temp = dfltName.substring(0,dfltName.indexOf('Number'));
             ele.AccountName =  dfltName.substring(0,dfltName.indexOf('Number'));
          }
          else if(dfltName.indexOf('No.') > -1){
            temp = dfltName.substring(0,dfltName.indexOf('No.'));
            ele.AccountName =  dfltName.substring(0,dfltName.indexOf('No.'));
          }
         
          else if(dfltName.indexOf('No') == -1){
            temp = dfltName
            ele.AccountName =  dfltName
          }
         
         
          ele.AccountDesc = temp;
        })
        this.bmlfswitchTab(this.mstabType,this.mstabTypeSmall);
        this.mbIsLoading = false;
        if (res == null) {
          this.mbCustomControl = true;
          this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('UNITOFMEASURECONFIGURATION.MESSAGE.NO_DATA_MSG'));
          return false;
        }
       
        if(this.AccountData.length > 0){
          this.mbIsChange = false;
        }
        this.mbCustomControl = true;
      }, err => {
        this.mbIsLoading = false;
        console.log(err);
      })
    }
  
    bmlfonGlobalOnChangesFn(psFieldName, pobjFieldData, piIndex = null) {
      if (pobjFieldData == null) {
        return false;
      }
      this.mbIsChange = true;
      this.mbIsSaveDone = false;
    }
    // Global focus Fn
    bmlfOnGlobalFocus(field, value) {
      if (typeof value === 'object' && value.constructor === Object) {
        let valueCopy = Object.assign({}, value);
        this.msLookupField = valueCopy[field];
      } else {
        this.msLookupField = value;
      }
    }
  
    // Global Blur Fn
    bmlfOnGlobalBlur(field, value) {
      if (field == 'lookup') {
        this.mbIsLookupFocus = false;
      }
    }
  
    // Get Lookup Description
    bmlfGetLookupDes(pobjData) {
      let pKeyCode = 'UNIT';
      let parrSetKeyData = [
          { KeyCode: pKeyCode, KeyValue1: pobjData.WtUOM, KeyValue2: 'WeightUOMKey', KeyDescription: '' },
          { KeyCode: pKeyCode, KeyValue1: pobjData.VolUOM, KeyValue2: 'VolumeUOMKey', KeyDescription: '' }
      ]
    
      let reqData: any = {
        JSON: JSON.stringify({
          HeaderData: parrSetKeyData,
          ScreenName:[{ ScreenName:this.msScreenName}],
          Response:[ { ResponseText:'', ErrorCode:''}]
        })
      };
    }
  
   
  
   
  
    // fieldExtender save fn
    bmlfSaveFieldExtender(screenKeyValue) {
      // FieldExtender -- start
      let jsonData = this.FieldExtender.bmlfSaveData(screenKeyValue);
      if (jsonData.length > 0) {
        this.FieldExtender.bmlfCallApi(jsonData).then(res => {}).catch(err => {console.log(err);});
      }
    }
  
    bmlfMendentFiledsMsg(evenType?) {
      let isMendentFiled = false;
      // if (this.mobjFormData.WtUOM == '' || this.mobjFormData.WtUOM == undefined) {
      //   let msg = this.utilService.bmgfonTranslate('UNITOFMEASURECONFIGURATION.MESSAGE.WEIGHTUOMBLANK');
       
      //   this.toastMessageService.notifyError(msg);
      //   isMendentFiled = true;
      //   return isMendentFiled;
      // }
      // if (this.mobjFormData.VolUOM == '' || this.mobjFormData.VolUOM == undefined) {
      //   let msg = this.utilService.bmgfonTranslate('UNITOFMEASURECONFIGURATION.MESSAGE.VOLUMEUOMBLANK');
      //   this.toastMessageService.notifyError(msg)
      //   isMendentFiled = true;
      //   return isMendentFiled;
      // }
      
      return isMendentFiled;
    }
  
    //Close Task MODAL
    bmlfonCloseTaskModal(pobjData) {
      this.mbIsTaskModal = pobjData.isTaskModal;
    }

    //  switch tab
    bmlfswitchTab(type: any, smallType) {
      this.mstabType = type;
      this.mstabTypeSmall = smallType;
      if(this.mstabType){
          this.AccountData.forEach(ele=>{
            if(ele.DfltType == smallType){
              ele.className='active';
            }
            else{
              ele.className='deactive'; 
            }
          })
      }
    }
    bmlfViewHelpDoc() {
      window.open('../../../../assets/help files/Default Account Setup.pdf', '_blank');
  }

  //Open Task MODAL
bmlfOpenTaskModal(taskType) {
  this.mobjTaskData = {};
  this.mobjTaskData.taskType = taskType;
  if (taskType == 'myTask') {
    this.mobjTaskData.title = this.utilService.bmgfonTranslate('MYTASK');
    this.mobjTaskData.innerRecord = this.mobjUserData;
  } else if (taskType == 'recordTask') {
    this.mobjTaskData.title = this.utilService.bmgfonTranslate('TASKFORRECORD');
    let pobjFormRec:any = {};
    pobjFormRec.formName = this.msScreenName;
    pobjFormRec.primeKey = '0';
    this.mobjTaskData.innerRecord = pobjFormRec;
  }
  this.mbIsTaskModal = true;
}
  }