import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';
import { Observable } from 'rxjs';
import { ODataModel } from 'src/app/core/models/odata.model';

@Injectable({
  providedIn: 'root'
})
export class DashboardMasterSetupService {

  constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) { }

  public bmlfonGetWidgedData(headerData: any): Observable<ODataModel> {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetWidgedData`;
    return this.httpExt.post<ODataModel>(url, headerData, null);
  }

  public BMGFDeleteWidgedData(headerData: any): Observable<any> {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFDeleteWidgedData`;
    return this.httpExt.post<any>(url, headerData, null);
  }

  public BMGFSaveWidgedData(reqData, type): Observable<any> {
    if (type == "I") {
      const url = `${this.appSettings.ServerAPIEndpointnew}/bme/BMGFSaveWidgedData`;
      return this.httpExt.post<any>(url, reqData, null);
    } else {
      const url = `${this.appSettings.ServerAPIEndpointnew}/bme/BMGFSaveWidgedData`;
      return this.httpExt.post<any>(url, reqData, null)
    }
  }
  public BMGDeleteFlowChartLineData(reqData): Observable<any> {
    const url = `${this.appSettings.ServerAPIEndpointnew}/bme/BMGFDeleteFlowChartLineData`;
    return this.httpExt.post<any>(url, reqData, null)
  }

}
