    bmlfOnGlobalFocus(field, value) {
        if (typeof value === 'object' && value.constructor === Object) {
            let valueCopy = Object.assign({}, value);
            this.msLookupField = valueCopy[field];
        } else {
            this.msLookupField = value;
        }
    }

    //force lowercase letter to uppercase
    onInput(e) {
        return e.target.value.toUpperCase();
    }

    setLookupTitle(psfield) {

        let title = '';
        if (psfield == 'Module') {
            title = 'REPORTSCHEDULESETUP.MODULELOOKUP'
        }
        if (psfield == 'ReportName') {
            title = 'REPORTSCHEDULESETUP.REPORTNAMELOOKUP'
        }
        if (psfield == 'ParameterName') {
            title = 'REPORTSCHEDULESETUP.PARAMETERLOOKUP'
        }
        if (psfield == 'Email' || psfield == 'Value') {
            title = 'REPORTSCHEDULESETUP.FIELDLOOKUP'
        }
        if (psfield == 'ReportScheduleSetupList') {
            title = 'REPORTSCHEDULESETUP.REPORTSCHEDULESETUPLIST'
        }
        return title;
    }
    // Description expanded Modal
    bmlfDescriptionModal(event, field) {
        this.msFieldName = field;
        if (event == 'open') {
            this.mbDescriptionDialog = true;
            if (field == 'Content') {
                this.msExpanderText = this.mobjModel.Content
            }
            if(field == 'ParamSql'){
                this.msExpanderText = this.mobjModel.ParameterSQL;
            }
        }
    }
    bmlfCloseDescriptionModal() {
        this.mbDescriptionDialog = false;
        if (this.msFieldName == 'Content') {
            this.mobjModel.Content = this.msExpanderText
        }
        if (this.msFieldName == 'ParamSql') {
            this.mobjModel.ParameterSQL = this.msExpanderText
        }
    }

    bmlfonSelectPeriod(period) {
        

        if(period == 0){
            this.mobjModel.SendEvery = 'Day'
        }

        this.mobjModel.OnNDay = 0;
        if (period == 'Day') this.msSendEvery = 0;
        if (period == 'Week') this.msSendEvery = 1;
        if (period == 'Month') this.msSendEvery = 2;
        if (period == 'Quarter') this.msSendEvery = 3;
        if (period == 'Year') this.msSendEvery = 4;
        // Day,quarter,year
        if (period == "Day" || period == "Quarter" || period == "Year") {
            this.mbIsDisableNthDay = true;
        } else {
            this.mbIsDisableNthDay = false;
        }
    }

    bmlfonSelectMailTo() {
        this.mobjModel.MailTo='';
    }
    bmlfonSelectType(type) {
        this.mobjModel.ReportName='';
        this.mobjModel.Module='';
        this.mobjModel.ParameterName='';
        this.marrGridData=[];
        if (type == 'Standard') {
            this.marrGridData.forEach(ele => {
                ele.User11 = 0;
            })
        } else {
            this.marrGridData.forEach(ele => {
                ele.User11 = 1;
            })
        }
    }
    bmlfonChangeType(type,rowIndex){
    if(type == 'SQLVal' || type == 'Text'){
        this.marrGridData[rowIndex]['Value']='';
    } else if(type == 'True/False'){
        this.marrGridData[rowIndex]['Value']='True';
    }
    }
    bmlfSetSendEvery(sendEvery) {
        if (sendEvery == 0) {
            this.mobjModel.SendEvery = 'Day';
        }
        if (sendEvery == 1) {
            this.mobjModel.SendEvery = 'Week';
        }
        if (sendEvery == 2) {
            this.mobjModel.SendEvery = 'Month';
        }
        if (sendEvery == 3) { 
            this.mobjModel.SendEvery = 'Quarter';
        }
        if (sendEvery == 4) {
            this.mobjModel.SendEvery = 'Year';
        }
    }
