    bmlfCopyData() {
        let HeaderData = Object.assign({}, this.mobjAlertSetupModel);
        HeaderData.AlertID = "";
        let parrCopyData = {
            HeaderData: HeaderData,
            prdKey: this.mobjAlertSetupModel.AlertID
        }
        localStorage.setItem('copyAlertSetupData', JSON.stringify(parrCopyData));
        this.bmlfRefreshForm();
    }

