     /**---Start----Add field button Code----- */
    bmlfOpenLookupAddField(psField, psScreenName, psLookUpKey, psEvent, psValue, psType, psEleId, psTitle = "", pnumRowIdx = null,FilterValue){
         if(!this.mobjAlertSetupModel.SQL){
            this.bmlfOpenErrorBlock('AlertSetup.MSG.SQLNOTBALNK');
           return false;
         }
        this.mbIsLoading = true;
        this.marrLookupGridData = [];
        this.mobjLookupKeyData = {};
        this.mobjLookupKeyData.title = this.utilService.bmgfonTranslate('AlertSetup.SQLFIELDSLOOKUP')
        let pobjOData = { HeaderData: [], Response: [] };
          pobjOData.HeaderData.push({
            "SQL": this.mobjAlertSetupModel.SQL
          });
          pobjOData.Response.push({
              "ResponseText": "",
              "ErrorCode": ""
          })

        let psJData = JSON.stringify(pobjOData);
        let psTempjData = JSON.stringify(psJData);
        let psJson = '{"JSON":' + psTempjData + '}';
        this.formService.bmlfGetFieldsFromSQL(psJson).subscribe(res => {
            this.mbIsLoading = false;
            if (res == null) {
                this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
                return false;
            }
           if (Object.entries(res.FieldData).length > 0 && res.FieldData.length > 0) {
                res.FieldData.forEach((ele) => {
                    Object.keys(ele).forEach((key) => {
                        let psKey = key.toUpperCase();
                        if (psKey.search('DATE') != -1) {
                            if (ele[key] && ele[key] != '') {
                                ele[key] = moment(ele[key]).format(this.msCustomDateFormat.toUpperCase());
                            } else {
                                ele[key] = moment().format(this.msCustomDateFormat.toUpperCase());
                            }
                        }
                    });
                })
                  this.marrLookupGridData = res.FieldData;
                  this.mnumLineItemRowIndex =  null;
                  this.mobjLookupKeyData.field = psField;
                  this.mobjLookupKeyData.gridData = this.marrLookupGridData;
                  this.mobjLookupKeyData.type = psField;
                  this.mobjLookupKeyData.eleId = psEleId;
                if (psValue) {
                    this.mobjLookupKeyData.searchValue = psValue.toString();
                } else {
                    this.mobjLookupKeyData.searchValue = '';
                }
              if ( psValue) {
                    let parrFilterdArr: any = [];
                    /** --- Check : enter key matching record found. if find single row then direct call save lookup function --*/
                    let pobjFirstInput = Object.keys(this.marrLookupGridData[0])[0];
                    parrFilterdArr = this.marrLookupGridData.filter(obj => {
                      if(obj[pobjFirstInput] != null ){
                            if ( obj[pobjFirstInput].toUpperCase() == psValue.toUpperCase()) {
                              return obj;
                          }
                      }
                    });
                    if (parrFilterdArr.length == 1) {
                        this.mobjLookupKeyData.value = parrFilterdArr[0];
                        this.mobjLookupKeyData.valueKey = parrFilterdArr[0][pobjFirstInput];
                        this.bmlfSaveLookupField(this.mobjLookupKeyData);
                        this.mbIsAddFieldLookup = false;
                    } else {
                        this.mbIsAddFieldLookup = true;
                    }
                } else if (psEvent == 'click') {
                    this.mbIsAddFieldLookup = true;
                }
            } else {
                this.bmlfOpenErrorBlock(res.Response[0].ResponseText);
           }
        }, err => {
            console.log(err);
            this.mbIsLoading = false;
        })
    }

    

 letterCounter (x) {
  return x.replace(/[^a-zA-Z]/g, '').length;
}

  /** save lookup field data */
bmlfSaveFieldLookup(pobjEvent) {
  this.mbIsLoading = false;
  this.mbIsAddFieldLookup = false;
    if (pobjEvent.type == 'ALERTCOMMENT') {
        this.mbIsChangeData = true;

        if(this.letterCounter(this.mobjAlertSetupModel.AlertComment) > 950){
            this.bmlfOpenErrorBlock('Alert Comment not allow more then 1000 character.');
            return
        }
         console.log(this.letterCounter(this.mobjAlertSetupModel.AlertComment))
          this.mobjAlertSetupModel.AlertComment += '['+ pobjEvent.valueKey +']';
          document.getElementById(pobjEvent.eleId).focus();
    }else if(pobjEvent.type == 'SUBJECT'){
          this.mobjAlertSetupModel.Subject += '['+ pobjEvent.valueKey +']';
      }
    }

    bmlfCloseFieldLookup(pobjEvent) {
      this.mbIsAddFieldLookup = pobjEvent.isLookupModal;
      document.getElementById(pobjEvent.eleId).focus();
    }
