import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from 'src/app/core/guards/auth.guard';
import { DashboardListComponent } from './components/dashboard-list/dashboard-list.component';
import { ConfigurationComponent } from './configuration.component';
import { DashboardManageComponent } from './components/dashboard-setup/dashboard-setup.component'
import { ModuleSetupComponent } from './components/module-setup/module-setup.component'
import { EmailLayoutMasterListComponent } from './components/email-layout-master/email-layout-master-list/email-layout-master-list.component';
import { EmailLayoutMasterManageComponent } from './components/email-layout-master/email-layout-master-manage/email-layout-master-manage.component'
import { AlertSetupListComponent } from './components/alert-setup/alert-setup-list/alert-setup-list.component';
import { AlertSetupManageComponent } from './components/alert-setup/alert-setup-manage/alert-setup-manage.component';
import {UnitOfMeasureConfigurationComponent} from './components/UOM-configuration/unit-of-measure-configuration.component';
import {DefaultAccountSetupComponent} from './components/default-account-setup/default-account-setup.component';
import { DocumentAuthorizationListComponent } from './components/document-authorization/document-authorization-list/document-authorization-list.component';
import { DocumentAuthorizationManageComponent } from './components/document-authorization/document-authorization-manage/document-authorization-manage.component';
import { DashboardMasterSetupComponent } from './components/dashboard-master-setup/dashboard-master-setup.component';
import { DashboardMasterSetupListComponent } from './components/dashboard-master-setup-list/dashboard-master-setup-list.component';
import {ReportScheduleSetupListComponent} from './components/report-schedule-setup/report-schedule-setup-list/report-schedule-setup-list.component';
import {ReportScheduleSetupManageComponent} from './components/report-schedule-setup/report-schedule-setup-manage/report-schedule-setup-manage.component';
import { SopConfiguratorComponent } from './components/sop-configurator/sop-configurator/sop-configurator.component';
import { DocumentTagSetupComponent } from './components/document-tag-setup/document-tag-setup.component';
const routes: Routes = [
    {
        path: '',
        component: ConfigurationComponent,
        children: [
            { path: '', redirectTo: 'configuration', pathMatch: 'full' },
            { path: 'dashboard', component: DashboardListComponent, canActivate: [AuthGuard] },
            { path: 'dashboard/manage', component: DashboardManageComponent, canActivate: [AuthGuard] },
            { path: 'module-setup', component: ModuleSetupComponent, canActivate: [AuthGuard] },
            { path: 'email-layout-master', component: EmailLayoutMasterListComponent, canActivate: [AuthGuard] },
            { path: 'email-layout-master/manage', component: EmailLayoutMasterManageComponent, canActivate: [AuthGuard] },
            { path: 'alert-setup', component: AlertSetupListComponent, canActivate: [AuthGuard] },
            { path: 'alert-setup/manage', component: AlertSetupManageComponent, canActivate: [AuthGuard] },
            { path: 'unit-of-measure-configuration', component:UnitOfMeasureConfigurationComponent,canActivate: [AuthGuard] },
            { path: 'default-account-setup', component:DefaultAccountSetupComponent,canActivate: [AuthGuard]},
            { path: 'document-authorization', component:DocumentAuthorizationListComponent,canActivate: [AuthGuard]},
            { path: 'document-authorization/manage', component:DocumentAuthorizationManageComponent,canActivate: [AuthGuard]},
            { path: 'dashboard-master-setup/manage', component:DashboardMasterSetupComponent,canActivate: [AuthGuard]},
            { path: 'dashboard-master-setup-list', component:DashboardMasterSetupListComponent,canActivate: [AuthGuard]},
            { path: 'report-schedule-setup', component:ReportScheduleSetupListComponent, canActivate:[AuthGuard]},
            { path: 'report-schedule-setup/manage', component:ReportScheduleSetupManageComponent, canActivate:[AuthGuard]},
            { path: 'sop-configurator', component:SopConfiguratorComponent, canActivate:[AuthGuard]},
            { path: 'document-tag-setup', component: DocumentTagSetupComponent, canActivate:[AuthGuard]}
        ]
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class ConfigurationRoutingModule { }




