import { Component, OnInit, Renderer2 } from '@angular/core';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { Router, ActivatedRoute } from '@angular/router';
import { DateFormatService } from 'src/app/core/services/dateFormat.service';
import { ToastMessageService } from 'src/app/core/services/toast-message.service';
import { AppUtility } from 'src/app/core/services/utility.services';
import { UserFieldService } from 'src/app/shared/http/userfield.service';
import { DocumentTagSetup } from '../../models/document-tag-setup.modal';
import { DocumentTagSetupService } from '../../http/document-tag-setup.service';

@Component({
  selector: 'app-document-tag-setup',
  templateUrl: './document-tag-setup.component.html',
  styleUrls: ['./document-tag-setup.component.scss']
})
export class DocumentTagSetupComponent implements OnInit {
  public mbIsLoading: boolean = false;
  public msScreenName: string = 'Document Tag Setup';
  public mbIsChangeData: boolean = false;
  public msCustomDateFormat: string = "";
  /**---Object Initialize---- */
  public mobjModel: DocumentTagSetup = new DocumentTagSetup();
  public mobjUserData: any = {};
  public msCommodKey: any;
  public mArrBinGrid1Col: [];
  public gridDataList: any;
  public gridView: any;
  public mobjGridSettings: any;
  public mbSaveDialogView: boolean = false;
  public mbIsCloseMode: boolean = false;
  public isValid: boolean = false;
  public isCheckValid: boolean = false;

  constructor(public router: Router, public route: ActivatedRoute,
    public renderer: Renderer2,
    private lsService: LocalStorageService,
    private formService: DocumentTagSetupService,
    private toastMessageService: ToastMessageService,
    public dateFormatService: DateFormatService, public objToastService: ToastMessageService,
    public utilService: AppUtility,
    public userFieldService: UserFieldService) {
  }

  ngOnInit(): void {
    /*--------- get screen permission --------*/
    this.utilService.getBMEScreePermission(this.msScreenName);
    /**--- Get default date format from service--- */
    this.msCustomDateFormat = this.dateFormatService.getDateFormat();
    this.mobjUserData = this.lsService.getUserSession();
    this.bmlfGetDocTagData();
  }

  // Get Data
  bmlfGetDocTagData() {
    this.mbIsLoading = true;
    let pobjOData = { HeaderData: [], Response: [] };
    pobjOData.HeaderData.push({
      "Key": this.msCommodKey
    });
    pobjOData.Response.push({
      "ResponseText": "",
      "ErrorCode": ""
    })

    let psJData = JSON.stringify(pobjOData);
    let psTempjData = JSON.stringify(psJData);
    let psJson = '{"JSON":' + psTempjData + '}';

    this.formService.bmlfGetSetupdData(psJson).subscribe(
      res => {
        console.log(res);
        let parrGridData = res.DocTagSetup;
        parrGridData.forEach(ele => {
          ele.RecDate = new Date(ele.RecDate);
        })
        this.gridDataList = parrGridData.map(ele => Object.assign({}, ele));
      }
      , error => {
        console.log(error);
      }
    ).add(() => {
      this.mbIsLoading = false;
    })

  }

  bmlfonAssignCustValues(custom) {
    this.mobjModel.CUSTOM1 = custom.CUSTOM1 ? custom.CUSTOM1 : null;
    this.mobjModel.CUSTOM2 = custom.CUSTOM2 ? custom.CUSTOM2 : null;
    this.mobjModel.CUSTOM3 = custom.CUSTOM3 ? custom.CUSTOM3 : null;
    this.mobjModel.CUSTOM4 = custom.CUSTOM4 ? custom.CUSTOM4 : null;
    this.mobjModel.CUSTOM5 = custom.CUSTOM5 ? custom.CUSTOM5 : null;
    this.mobjModel.CUSTOM6 = custom.CUSTOM6 ? custom.CUSTOM6 : null;
    this.mobjModel.CUSTOM7 = custom.CUSTOM7 ? custom.CUSTOM7 : null;
    this.mobjModel.CUSTOM8 = custom.CUSTOM8 ? custom.CUSTOM8 : null;
    this.mobjModel.CUSTOM9 = custom.CUSTOM9 ? custom.CUSTOM9 : null;
    this.mobjModel.CUSTOM10 = custom.CUSTOM10 ? custom.CUSTOM10 : null;
    this.mobjModel.User1 = custom.User1 ? custom.User1 : "";
    this.mobjModel.User2 = custom.User2 ? custom.User2 : "";
    this.mobjModel.User3 = custom.User3 ? custom.User3 : "";
    this.mobjModel.User4 = custom.User4 ? custom.User4 : "";
    this.mobjModel.User5 = custom.User5 ? custom.User5 : "";
    this.mobjModel.User6 = custom.User6 ? custom.User6 : null;
    this.mobjModel.User7 = custom.User7 ? custom.User7 : 0;
    this.mobjModel.User8 = custom.User8 ? custom.User8 : 0;
    this.mobjModel.User9 = custom.User9 ? custom.User9 : 0;
    this.mobjModel.User10 = custom.User10 ? custom.User10 : 0;
    this.mobjModel.User11 = custom.User11 ? custom.User11 : 0;
    this.mobjModel.User12 = custom.User12 ? custom.User12 : 0;

  }


  //Check Un-Check Order

  CheckValue() {
    
    if (this.isCheckValid == false) {
      if (this.gridDataList[0]['DocTagDisplay'] == true && this.gridDataList[1]['DocTagDisplay'] == false
        && this.gridDataList[2]['DocTagDisplay'] == false && this.gridDataList[3]['DocTagDisplay'] == false
        && this.gridDataList[4]['DocTagDisplay'] == false) {
        this.isCheckValid = true;
      }

      if (this.gridDataList[0]['DocTagDisplay'] == true && this.gridDataList[1]['DocTagDisplay'] == true
        && this.gridDataList[2]['DocTagDisplay'] == false && this.gridDataList[3]['DocTagDisplay'] == false
        && this.gridDataList[4]['DocTagDisplay'] == false
      ) {
        this.isCheckValid = true;
      }
      else if (this.gridDataList[0]['DocTagDisplay'] == true && this.gridDataList[1]['DocTagDisplay'] == true
        && this.gridDataList[2]['DocTagDisplay'] == true && this.gridDataList[3]['DocTagDisplay'] == false
        && this.gridDataList[4]['DocTagDisplay'] == false
      ) {
        this.isCheckValid = true;
      }
      else if (this.gridDataList[0]['DocTagDisplay'] == true && this.gridDataList[1]['DocTagDisplay'] == true
        && this.gridDataList[2]['DocTagDisplay'] == true && this.gridDataList[3]['DocTagDisplay'] == true
        && this.gridDataList[4]['DocTagDisplay'] == false) {
        this.isCheckValid = true;
      }
      else if (this.gridDataList[0]['DocTagDisplay'] == true && this.gridDataList[1]['DocTagDisplay'] == true
        && this.gridDataList[2]['DocTagDisplay'] == true && this.gridDataList[3]['DocTagDisplay'] == true
        && this.gridDataList[4]['DocTagDisplay'] == true) {
        this.isCheckValid = true;
      }
    }
    return;
  }
  bmlfOnCheckBoxSequence(rowIndex) {
    
    if (rowIndex > 0 && rowIndex < 4) {
      let beforeInd = rowIndex - 1;
      let afterInd = rowIndex + 1;
      if (this.gridDataList[beforeInd]['DocTagDisplay'] == false) {
        this.isCheckValid = false;
        return;
      }

      else if (this.gridDataList[rowIndex]['DocTagDisplay'] == false && this.gridDataList[afterInd]['DocTagDisplay'] == true) {
        this.isCheckValid = false;
        return;
      }
      else if (this.gridDataList[rowIndex]['DocTagDisplay'] == false && this.gridDataList[beforeInd]['DocTagDisplay'] == true) {
        this.isCheckValid = false;
        return;
      }

      else if (this.gridDataList[0]['DocTagDisplay'] == false) {
        this.toastMessageService.notifyError(this.utilService.bmgfonTranslate("DOCTAGSETUP.MSG.FIRSTROW"));
        return;
      }
    }

    else if (this.gridDataList[0]['DocTagDisplay'] == false) {
      this.toastMessageService.notifyError(this.utilService.bmgfonTranslate("DOCTAGSETUP.MSG.FIRSTROW"));
      return;
    }


  }


  bmlfValidData() {
    
    let RowNumbers: number = 0;
    let pbResult: boolean = true;
    this.gridDataList.forEach((ele, i) => {
      if (pbResult == true) {
        RowNumbers = RowNumbers + 1;
        // if (!ele.DocTagDisplay && RowNumbers == 1) {
        //   console.log(i)
        //   this.toastMessageService.notifyError((this.utilService.bmgfonTranslate('Enter Select Atlist ones Check Box')) + ': Row No. ' + RowNumbers);
        //   pbResult = false;
        // }
        if (ele.DocTagDisplay && ele.DocTagUserName == '') {
          this.toastMessageService.notifyError((this.utilService.bmgfonTranslate('DOCTAGSETUP.MSG.USERFIELDBLANK')) + " " + RowNumbers);
          pbResult = false;
          document.getElementById(i + 'txtTagUserName').focus();
          this.isValid = false;
          return;
        }
      }
      else {
        this.isValid = true;
      }
    });
    return pbResult;
  }
  // For Save Data

  bmlfOnSubmitTagData() {
    
    this.mbIsLoading = false;
    //For Check Permission
    if (!this.utilService.mbWrite) {
      this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    }

    if (!this.mbIsChangeData) {
      return;
    };

    if (!this.bmlfValidData()) {
      return;
    }

    // For Check Box Sequence Order
    this.CheckValue();

    if (!this.isCheckValid) {
      this.toastMessageService.notifyError(this.utilService.bmgfonTranslate("DOCTAGSETUP.MSG.SEQUENCE"));
      return;
    }

    // if (!this.isValid) {
    //   this.toastMessageService.notifyError(this.utilService.bmgfonTranslate("DOCTAGSETUP.MSG.USERNAME"));
    //   return;
    // }


    this.bmlfOnSaveTagData();
    this.mbIsChangeData = false;
  }

  bmlfOnSaveTagData(): void {
    
    this.mbIsLoading = true;
    let pobjJData = {
      documenttagsetup: this.gridDataList,
      Response: [{
        "ResponseText": "",
        "ErrorCode": "",
      }]
    };

    let psJData = JSON.stringify(pobjJData);
    let psTempjData = JSON.stringify(psJData);
    let psJson = '{"JSON":' + psTempjData + '}';

    this.formService.bmlfSaveSetupData(psJson).subscribe(
      res => {
        console.log(res);
        this.mbIsLoading = false;
        if (res.Response[0].ErrorCode == "") {
          this.gridDataList = res.documenttagsetup;
          this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate("COMMON.MSG.SAVE"));
          //this.bmlfRefreshForm();         
          if(this.mbIsCloseMode) {
            this.mbIsCloseMode = false;
            this.bmlfCloseDocTagSetup();//Bug 182388
          }
          else {
            this.bmlfGetDocTagData();
            this.isCheckValid = false;
          }
        } 
      }
    );
  }

  // On Refresh Form
  bmlfRefreshForm() {
    this.router.navigate(["/main/configuration/document-tag-setup"]);
  }

  // On Change in Field
  bmlfOnFormFieldChange() {
    this.mbIsChangeData = true;
  }


  // For Close Form
  bmlfCloseForm(): void {
    if (this.mbIsChangeData == true) {
      this.mbSaveDialogView = true;
      this.mbIsCloseMode = true;
    }
    else {
      this.router.navigate(["/main/dashboard/dashboardpage"]);
    }
  }

  // For Close Form 
  bmlfCloseDocTagSetup(): void {
      this.router.navigate(["/main/dashboard/dashboardpage"]);
  }
  // For Error Message
  bmlfOpenErrorBlock(psMsg): void {
    this.toastMessageService.notifyError(this.utilService.bmgfonTranslate(psMsg));
  }

  // Close Confirmation
  bmlfOnConfirmModal(psype) {
    if (psype == 'YES') {
      this.bmlfOnSubmitTagData();
      this.router.navigate(['/main/dashboard/dashboardpage']);
      this.mbSaveDialogView = false;
    } else if (psype == 'NO') {
      this.mbSaveDialogView = false;
      this.router.navigate(['/main/dashboard/dashboardpage']);
    }
    else {
      this.mbSaveDialogView = false;
    }
  }

  // For Help PDF 
  bmlfViewHelpDoc() {
    window.open('../../../../assets/help files/Document Tag Setup.pdf', '_blank');
  }

}
